module.exports = {"header.experience":"Experiencia","header.services":"Servicios","header.contact":"Contacto","header.language":"Lenguaje","introduction.header":"Hola, soy Andres Muñoz.","introduction.body":"Desarrollador web <strong>back-end</strong> y <strong>front-end</strong>. Trabajo con la arquitectura de microservicios y practico la cultura de DevOps.","footer.back-to-top":"Volver arriba","experience.header":"Experiencia","experience.introduction":"Soy desarrollador de aplicaciones web. Tengo un conocimiento avanzado del lenguaje de programación <strong>Java</strong> y del <strong>Spring Framework</strong>. Tengo experiencia con <strong>Angular</strong> y <strong>React</strong>. Soy un seguidor de la arquitectura de <strong>Microservicios</strong> y la cultura de <strong>DevOps</strong>. Tengo experiencia en <strong>liderar</strong> a personas y me gusta aprender sobre todo para poder contribuir en casi cualquier situación; certificado en <strong>Oracle</strong>, Oracle Certified Professional Java 8 (<a href=\"https://www.youracclaim.com/badges/23633bfa-a00f-4f89-9aa8-e4ffd6fd54d2/public_url\" target=\"_blank\">Distintivo digital</a>) y Oracle Certified Associate Java 8 (<a href=\"https://www.youracclaim.com/badges/754583e4-99e4-4cef-9774-0752568a90d4/public_url\" target=\"_blank\">Distintivo digital</a>); certificado en <strong>Linux Systems Administration</strong> aprovisionada por CompTIA (<a href=\"https://www.certmetrics.com/comptia/public/verification.aspx?code=TWKMEJXV4R36F29P\" target=\"_blank\">enlace de verificación</a>) y <strong>Linux Professional Institute</strong> (LPI) adicional cuento con el certificado <strong>AWS Certified Developer – Associate</strong> (<a href=\"http://aws.amazon.com/verification\" target=\"_blank\">enlace de verificación</a> – Código T2N1DSFKJN1QQ0KW).","experience.item.website":"Sitio Web","experience.item.location":"Ubicación","experience.item.jobTitle":"Nombre del cargo","experience.item.dateRange":"Fecha","experience.item.details":"Detalles","experience.tomtom.location":"Berlin, Alemania","experience.tomtom.jobTitle":"Ingeniero de Software","experience.tomtom.dateRange":"2019-02 - 2019-07","experience.tomtom.details0":"Migrar la configuración de la infraestructura de Puppet a Ansible.","experience.tomtom.details1":"Implementar nuevas características en servicios críticos.","experience.tomtom.details2":"Hacer mejoras en la alta disponibilidad de servicios.","experience.tomtom.details3":"Hacer mejoras en las habilidades blandas del equipo y rendimiento con la metodología Scrum.","experience.beitech.location":"Bogotá, Colombia","experience.beitech.jobTitle":"Desarrollador Full Stack y DevOps","experience.beitech.dateRange":"2017-09 - Presente","experience.beitech.details0":"Trabajar en la lógica del Front-end usando AngularJS.","experience.beitech.details1":"Trabajar en la lógica del Back-end utilizando Java y Spring Framework.","experience.beitech.details2":"Automatizar configuraciones de entornos de desarrollo con Vagrant, shell scripts y Ansible.","experience.beitech.details3":"Diseñar e implementar infraestructura en la nube utilizando AWS.","experience.beitech.details4":"Realizar administración general de varias instancias de Linux.","experience.zenware.location":"Bogotá, Colombia","experience.zenware.jobTitle":"Desarrollador Java","experience.zenware.dateRange":"2016-03 - 2017-07","experience.zenware.details0":"Realizar análisis, diseño e implementación de una aplicación web para la gestión de torneos de robótica. Esta aplicación ejecutó operaciones CRUD en los diferentes conceptos de dominio, como por ejemplo equipos, participantes, torneos, categorías, partidos, etc. Esta incluyó responsive design y compatibilidad con dispositivos móviles y los navegadores de internet más comunes. Y finalmente, esta tenía soporte para los idiomas inglés, español y japonés.","experience.zenware.details1":"Realizar análisis, diseño e implementación de las librerías base de todos los proyectos web desarrollados internamente, que incluyen mejores prácticas y patrones de diseño, protección contra los vectores de ataque más comunes (seguridad) y varias mejoras para SEO.","experience.zenware.details2":"Escribir scripts bash de propósito general para la configuración de entornos de desarrollo y producción.","experience.avantel.location":"Bogotá, Colombia","experience.avantel.jobTitle":"Desarrollador Full Stack","experience.avantel.dateRange":"2015-03 - 2016-03","experience.avantel.details0":"Agregar AngularJS a 2 aplicaciones móviles híbridas implementadas previamente con solo HTML, CSS, Javascript y Cordova. Esto se hizo para hacerlas más fáciles de mantener introduciendo una estructura más organizada que permita reutilizar componentes. Además, uso de código nativo para Android e iOS para algunos requisitos. Y, por último, agreguar pruebas unitarias a los servicios REST que consumen estas aplicaciones.","experience.soluciones360.location":"Bogotá, Colombia","experience.soluciones360.jobTitle":"Desarrollador Java","experience.soluciones360.dateRange":"2014-07 - 2015-02","experience.soluciones360.details0":"Aplicación de escritorio con base de datos local (sqlite), importación de datos desde archivos Excel, generación de reportes personalizados en PDF, manejo de correos, sincronización a través servicios REST usando un algoritmo personalizado de cifrado (llave AES para el grueso de la transmisión y RSA para el intercambio de la llave de sesión). Interfaz personalizada (Custom Look and Feel) con distribuciones para Windows (EXE), MAC (DMG APP) y Linux (TARGZ).","experience.envasadorasycentrifugas.location":"Palmira, Colombia","experience.envasadorasycentrifugas.jobTitle":"Programador","experience.envasadorasycentrifugas.dateRange":"2013-01 - 2014-06","experience.envasadorasycentrifugas.details0":"Aplicación web (JSF) con diseño responsivo y contenido dinámico que permite la creación, edición y eliminación de artículos detallando los diferentes proyectos en los cuales la compañía ha trabajado en el pasado.","experience.envasadorasycentrifugas.details1":"Aplicación de escritorio para el manejo de nómina, con reportes en Excel e impresiones.","experience.envasadorasycentrifugas.details2":"Automatización de centrifugas de azucar, 6 de tipo batch y 3 de tipo continuas, usando PLCs(S7-200, S7-1200) y HMIs (TP 177B de 6”).","experience.envasadorasycentrifugas.details3":"Diseño 3D en Solidworks para diferentes tipos de dispositivos mecánicos con sus respectivos planos para construcción.","services.header":"Servicios","services.introduction":"Me gusta describirme como una persona versátil porque a través del tiempo he estado desempeñando diferentes roles. Empecé principalmente como desarrollador back-end, luego aprendí desarrollo de front-end y finalmente aprendí DevOps. A algunas personas les gusta usar la frase \"Aprendiz de todo, maestro de nada\" cuando se refiere a lo que hago, pero, en realidad, ese no es el caso debido a que el desarrollo de Back-end es mi especialidad, y el desarrollo de Front-end y DevOps fueron cosas que aprendí porque quería aprender todo lo que podía.","services.backend.header":"Desarrollador Back-end","services.backend.introduction":"Ser desarrollador Back-end es mi especialidad, esto gracias a todos los proyectos en los que he trabajado, principalmente en la lógica de negocio del lado del servidor, asegurando el rendimiento y que la calidad del código fuera a mejor.","services.backend.languages":"En cuanto a lenguajes de programación, tengo gran familiaridad con Java, he adquirido amplia experiencia en dicho lenguaje a través de mi carrera sin mencionar las certificaciones de Oracle que poseo, el segundo lenguaje que quiero mencionar es PHP, trabaje con este por el periodo de unos meses y finalmente C# el cual quiero aplicar en un proyecto profesional prontamente.","services.backend.databases":"Con respecto a bases de datos relacionales mi experiencia es con MySQL ya que he desarrollado muchas de las cosas que un DBA realiza. Además de las cosas comunes como instalar y configurar un servidor de base de datos, diseñar y crear las estructuras de tablas y las bases de datos, manejar usuarios, tomar y restaurar backups, monitorear los recursos usados por el servidos de base de datos, de lo que siento más orgullo es la puesta a punto de consultas críticas con la ayuda de planes de ejecución, restructura de datos y/o implementación de índices en el lugar correcto. Otra base de datos relacional es Postgres, en la cual he realizado puesta a punto de consultas críticas.  Además de las ya mencionadas, he trabajado también con Oracle, que dada mi experiencia en la mejora de consultas no es para mí un inconveniente realizar lo mismo con esta. De igual modo con las bases de datos NoSQL MongoDB y DynamoDB me familiarizo fácilmente.","services.backend.scalability":"Cabe destacar mi habilidad en el uso de diferentes estrategias para escalar aplicaciones, dependiendo de los recursos disponibles, ejemplo, aplicaciones que puedan ser escaladas al replicarlas, al dividirlas en más pequeñas, al usar cache agresivamente, al evitar el uso de estado de ellas y demás.","services.backend.listIntroduction":"Algunos de los stacks que he usado son los siguientes:","services.devops.header":"Ingeniero DevOps","services.devops.introduction":"A lo largo de mi carrera he tenido la oportunidad de ejercer diferentes habilidades que tiene un DevOps y me permitieron tener una visión más general y útil para los equipos de la que tenía.","services.devops.linuxadmin":"En la mayor parte del proyecto en el que he trabajado, realice las tareas de un administrador de sistemas Linux, instale y configure innumerables paquetes para distribuciones basadas en Fedora (Red Hat Enterprise Linux y CentOS) y Debian (Mint, Ubuntu), incluidos sus servicios cuando no contaban con uno. En segundo lugar, administré usuarios y grupos con sus respectivas propiedades, configuré logs del sistema y de aplicaciones, mantuve cron Jobs para tareas automatizadas; configuré y solucione problemas de interfaces de red, tablas de rutas y componentes relacionados cuando fue necesario. Finalmente hice uso intensivo de Shell scripts para automatizar todo tipo de tareas repetitivas, gracias a esta experiencia logre certificarme.","services.devops.aws":"En el proyecto en el que me encuentro actualmente, configure desde cero la arquitectura utilizada para las aplicaciones desplegadas en AWS. Empecé definiendo los usuarios y las políticas que les permiten acceder a los diferentes servicios; posterior a esto, configure las VPCs con sus subredes, grupos de seguridad, puertas de enlace de Internet, tablas de rutas e incluso dispositivos NAT para subredes privadas. Seguidamente, configuré varios entornos Elastic Beanstalk para producción, stanging y QA, que incluían instancias EC2 en Auto Balancing Groups y varios Application Load Balancers. Desde luego, se crearon varias instancias de base de datos RDS para cada uno de los entornos y finalmente debido a que la infraestructura tiende a volverse aún más compleja con el tiempo, la puse en control de versiones con la ayuda de CloudFormation, es decir, automaticé la administración de esta infraestructura; luego de esta experiencia obtuve una certificación.","services.devops.vagrantAnsible":"Cuando comencé en una de las empresas para la que trabajé en el pasado, recuerdo que todo lo que tenía era una serie de manuales para configurar el entorno de desarrollo. El problema era que esos manuales ya no estaban actualizados, ya que la última modificación se realizó meses atrás, sin mencionar que no todos los cambios en el entorno de desarrollo estaban documentados. Así que decidí cambiar eso y optar por una herramienta como Vagrant, que me permitió \"documentar\" fácilmente la configuración de una manera que sea ejecutable y replicable para cualquier persona en cualquier sistema operativo. Originalmente escribí este aprovisionamiento de configuración con sólamente shell scripts, pero luego me di cuenta de que esto era difícil de mantener debido a las limitaciones de bash. Así que integré Ansible, lo que hizo que las cosas fueran mucho más fáciles de mantener para mí y los otros desarrolladores. Al final, utilicé Ansible para cualquier aprovisionamiento de configuración que necesitaba, no solo para Vagrant, sino también para las instancias de AWS EC2, como por ejemplo la configuración de la instancia de Integración Continua y Despliegue Continuo, que incluía Jenkins y SonarQube.","services.devops.jenkinsSonarQube":"En varios proyectos, tuve la oportunidad de configurar desde cero instancias de Linux dedicadas para la Integración Continua y Despliegue Continuo. Primero, por supuesto usando Ansible, instalé y configuré Jenkins con varias Pipelines para cada combinación de proyectos y entornos. Estos Pipelines tenían un paso en que ejecutaba pruebas unitarias escritas con JUnit y varias otras herramientas. En segundo lugar, instalé y configuré SonarQube para análisis y control de código estático.","services.frontend.header":"Desarrollador Front-end","services.frontend.introduction":"Trabajar en desarrollo web no está completo sin front-end, es por esto que he tenido la experiencia con diferentes tecnologías durante estos años.","services.frontend.traditional":"Empecé hace tiempo con la forma tradicional de trabajar con tecnologías de front-end, es decir, páginas generadas por tecnologías de back-end. Primero utilice JSP para generar dinámicamente páginas, lo cual, es similar a la forma en que PHP genera las mismas; más adelante utilice JSF con Primefaces cuando JSP quedo en desuso. Finalmente, cuando la separación entre front-end y back-end fue más notoria, decidí generar páginas front – end dentro de los navegadores utilizando frameworks de JavaScript y consumiendo servicios web.","services.frontend.angular":"El primer framework de JavaScript que utilicé fue AngularJS. Integré este framework en varias aplicaciones móviles híbridas y algunas aplicaciones web, aunque, en ese momento, no había un gran soporte para algunas características de Javascript que me obligaban a utilizar soluciones temporales para algunos navegadores. El resultado general fue excelente debido a que este marco introdujo una mejor manera de mantener las cosas separadas y organizadas que hicieron que el código fuente fuera más fácil de mantener.","services.frontend.react":"Recientemente, comencé a aprender React para construir este portafolio web porque se ha convertido en una alternativa popular a Angular.","contact.header":"Contacto","contact.introduction":"Si mi trabajo es de su interés, no dude en contactarme a mi e-mail anfe0690 en gmail.com o usando el siguiente formulario:","contact.name":"Nombre","contact.email":"Correo electrónico","contact.replyto":"Responder a","contact.message":"Mensaje","contact.send":"Enviar"}

